@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700&display=swap");
.u-display-flex {
  display: flex;
}

.u-justify-center {
  justify-content: center;
}

.u-justify-start {
  justify-content: start;
}

.u-justify-end {
  justify-content: end;
}

.u-justify-between {
  justify-content: space-between;
}

.u-align-start {
  align-items: flex-start;
}

.u-align-end {
  align-items: flex-end;
}

.u-align-center {
  align-items: center;
}

.u-m-auto {
  margin: 0 auto;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0, 16, 61, 0.84);
}

.hidden {
  transform: translateX(100%);
  transition: transform 0.5s ease-in-out;
}

.visible {
  transform: translateX(0);
  transition: transform 0.5s ease-in-out;
}

*, *::before, *::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  color: #000000;
  font-family: "Raleway", sans-serif;
  margin: 0;
  padding: 0;
  height: 100%;
  position: relative;
  overflow-x: hidden;
}

img {
  max-width: 100%;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
ul li {
  font-size: 1rem;
}

a, li {
  text-decoration: none;
}

.heading1 {
  color: #000000;
  font-size: 2.5rem;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  line-height: 48px;
  letter-spacing: 0.2px;
  text-align: center;
}
@media (min-width: 520px) {
  .heading1 {
    font-size: 3em;
    line-height: 61px;
    text-align: start;
  }
}

.heading2 {
  color: #000000;
  font-size: 2rem;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  line-height: 38px;
  letter-spacing: 0.2px;
  text-align: center;
}
@media (min-width: 520px) {
  .heading2 {
    font-size: 2.75rem;
    text-align: start;
  }
}

.heading3 {
  color: #000000;
  font-size: 2.25rem;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  line-height: 36px;
  letter-spacing: 0.1px;
  text-align: center;
}
@media (min-width: 520px) {
  .heading3 {
    font-size: 2.75rem;
    text-align: start;
  }
}

.paragraph {
  color: #868686;
  font-size: 1.5rem;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  line-height: 38px;
  letter-spacing: 0.2px;
  text-align: center;
}
@media (min-width: 520px) {
  .paragraph {
    text-align: left;
  }
}

.label {
  color: #5956E9;
  font-size: 1.5rem;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  line-height: 36px;
  letter-spacing: 0.1px;
  text-align: center;
}
@media (min-width: 932px) {
  .label {
    text-align: left;
  }
}

a {
  color: #252B42;
  line-height: 38px;
  font-weight: 600;
}
a:hover {
  cursor: pointer;
}

.container {
  max-width: 1054px;
  margin: 0 auto;
  padding: 0 15px;
  overflow: visible;
}
.container--full {
  max-width: 1440px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.grid--3 {
  grid-template-columns: repeat(3, 1fr);
}
.grid--4 {
  grid-template-columns: repeat(4, 1fr);
}

.flex {
  display: flex;
  justify-content: space-between;
}

.col {
  width: 100%;
  padding: 15px;
  display: inline-block;
}
@media (min-width: 932px) {
  .col--md25 {
    flex: 0 0 25%;
    max-width: 25%;
  }
}
@media (min-width: 932px) {
  .col--md33 {
    flex: 0 0 33%;
    max-width: 33%;
  }
}
@media (min-width: 932px) {
  .col--md50 {
    flex: 0 0 50%;
    max-width: 50%;
  }
}
@media (min-width: 1280px) {
  .col--lg25 {
    flex: 0 0 25%;
    max-width: 25%;
  }
}
@media (min-width: 1280px) {
  .col--lg33 {
    flex: 0 0 33%;
    max-width: 33%;
  }
}
@media (min-width: 1280px) {
  .col--lg50 {
    flex: 0 0 50%;
    max-width: 50%;
  }
}
.col--hidden-mobile {
  display: none;
}
@media (min-width: 520px) {
  .col--hidden-mobile {
    display: inline-block;
  }
}

.header {
  padding-top: 24px;
  position: relative;
}
.header .flex {
  justify-content: space-between;
}

@media (min-width: 932px) {
  .header {
    padding-top: 60px;
  }
  .header .logo {
    align-items: center;
    display: inline-flex;
  }
}
.section {
  margin: 100px 0;
}

.showcase {
  overflow: hidden;
  margin: 60px 0;
}
@media (min-width: 520px) {
  .showcase {
    margin: 90px 0;
  }
}
.showcase .flex {
  flex-direction: column;
}
@media (min-width: 520px) {
  .showcase .flex {
    flex-direction: row;
    justify-content: start;
  }
}
.showcase__text {
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 520px) {
  .showcase__text {
    width: 60%;
    margin: 0;
    justify-content: start;
  }
}
.showcase h1 {
  margin-bottom: 24px;
}
.showcase p {
  margin-bottom: 44px;
}
.showcase__img {
  z-index: -10;
}
.showcase__img--desktop {
  display: none;
}
@media (min-width: 520px) {
  .showcase__img--desktop {
    display: inline-block;
    position: absolute;
    top: 0;
    right: 0;
    width: 45%;
  }
}
.showcase__img--mobile {
  position: relative;
  right: -50px;
}
@media (min-width: 520px) {
  .showcase__img--mobile {
    display: none;
  }
}

@media (min-width: 520px) {
  .application {
    margin-top: 200px;
  }
}
.application__title {
  font-weight: 600;
  font-size: 34px;
  line-height: 36px;
  text-align: center;
  margin-bottom: 76px;
}
@media (min-width: 520px) {
  .application__title {
    font-size: 2.75rem;
  }
}
.application__info {
  text-align: center;
  margin: 0 15px;
  padding-bottom: 30px;
}
@media (min-width: 520px) {
  .application__info .label {
    text-align: left;
  }
}
.application__info .heading2 {
  text-align: center;
  margin-top: 30px;
}
@media (min-width: 520px) {
  .application__info .heading2 {
    text-align: left;
  }
}
.application__info .paragraph {
  color: #868686;
  margin-top: 34px;
  text-align: center;
}
@media (min-width: 520px) {
  .application__info .paragraph {
    text-align: left;
  }
}
.application__img--desktop {
  display: none;
}
@media (min-width: 520px) {
  .application__img--desktop {
    display: inline-block;
  }
}
.application__img--mobile {
  display: inline-block;
  margin: 0 auto;
}
@media (min-width: 520px) {
  .application__img--mobile {
    display: none;
  }
}
.application .grid {
  grid-template-columns: 1fr;
}
@media (min-width: 520px) {
  .application .grid {
    grid-template-columns: 45% 55%;
  }
}

.ar .grid {
  grid-template-columns: 1fr;
}
@media (min-width: 932px) {
  .ar .grid {
    position: relative;
    grid-template-columns: 45% 55%;
    background: linear-gradient(72.77deg, #EDEEF6 11.72%, rgba(237, 238, 246, 0) 98.52%);
    border-radius: 50px;
    padding: 133px 55px;
  }
}
.ar .grid .ar__text {
  margin-bottom: 35px;
}
.ar .grid .ar__text .heading2 {
  text-align: center;
}
@media (min-width: 932px) {
  .ar .grid .ar__text .heading2 {
    text-align: left;
  }
}
.ar .grid .ar__text p {
  color: #868686;
  text-align: center;
  margin-top: 44px;
}
@media (min-width: 932px) {
  .ar .grid .ar__text p {
    text-align: left;
  }
}
.ar__img--mobile {
  display: inline-block;
  justify-self: center;
}
@media (min-width: 932px) {
  .ar__img--mobile {
    display: none;
  }
}
.ar__img--desktop {
  display: none;
}
@media (min-width: 932px) {
  .ar__img--desktop {
    display: inline-block;
    position: absolute;
    right: 0;
    top: -105px;
  }
}

.socialmedia__text {
  max-width: 500px;
  text-align: center;
  font-size: 1.625rem;
  font-weight: 700;
  line-height: 36px;
  justify-self: center;
}
@media (min-width: 932px) {
  .socialmedia__text {
    font-size: 2rem;
    line-height: 57px;
  }
}
.socialmedia__img {
  justify-self: center;
  padding-bottom: 65px;
}
.socialmedia .grid {
  grid-template-columns: 1fr;
  border-bottom: 1px solid #C9CADE;
}
@media (min-width: 932px) {
  .socialmedia .grid {
    grid-template-columns: 1fr 1fr;
  }
}

.footer {
  margin: 25px 0 80px 0;
}
.footer__logo {
  margin-bottom: 25px;
}
.footer p {
  font-size: 14px;
}
.footer .flex {
  flex-direction: column;
}
@media (min-width: 932px) {
  .footer .flex {
    flex-direction: row;
    align-items: center;
  }
}

button {
  border: none;
  background-color: transparent;
}

.btn {
  background-color: #5956E9;
  color: #fff;
  padding: 12px 25px;
  display: inline-block;
  cursor: pointer;
  border: 2px solid transparent;
  font-size: 0.9375rem;
  font-weight: 700;
  border-radius: 0.25rem;
  margin: 5px 0;
  transition: background-color 0.2s ease-in-out, border 0.2s ease-in-out, color 0.2s ease-in-out;
}
.btn:hover {
  color: #5956E9;
  background-color: #fff;
  border-color: #5956E9;
}
.btn--small {
  padding: 12px 25px;
}

.hamburger {
  display: inline-block;
  cursor: pointer;
  z-index: 20;
  border: none;
  background-color: transparent;
  width: 35px;
  height: 35px;
  margin-top: 5px;
}
.hamburger svg, .hamburger path {
  fill: #868686;
}
.hamburger:hover svg, .hamburger:hover path {
  fill: #5956E9;
}
@media (min-width: 932px) {
  .hamburger {
    display: none;
  }
}

.btn-menu {
  cursor: pointer;
}
.btn-menu:hover svg, .btn-menu:hover path {
  fill: #5956E9;
}

.navigation--desktop {
  display: none;
}
@media (min-width: 932px) {
  .navigation--desktop {
    display: block;
  }
  .navigation--desktop ul {
    display: flex;
    align-items: center;
  }
  .navigation--desktop ul li:last-child {
    margin-left: 120px;
  }
  .navigation--desktop a {
    padding: 10px;
    margin: 0 30px;
  }
  .navigation--desktop a:hover {
    cursor: pointer;
    color: #5956E9;
  }
  .navigation--desktop a.active {
    color: #5956E9;
  }
}
.navigation--mobile {
  background-color: #fff;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 20%;
  z-index: 100;
}
.navigation--mobile .menu {
  display: flex;
  flex-direction: column;
}
.navigation--mobile .menu__top {
  padding: 19px 24px;
  border-bottom: 1px solid #5956E9;
}
.navigation--mobile .menu__top-heading {
  font-size: 40px;
  font-weight: 700;
  color: #5956E9;
}
.navigation--mobile nav .menu-mobile__list {
  padding: 46px 0 22px 24px;
  border-bottom: 1px solid #5956E9;
}
.navigation--mobile nav .menu-mobile__list li {
  padding-bottom: 28px;
}
.navigation--mobile nav .menu-mobile__list li a:hover {
  cursor: pointer;
  color: #5956E9;
}
.navigation--mobile nav #btn-mobile {
  padding: 46px 24px;
}
@media (min-width: 932px) {
  .navigation--mobile {
    display: none;
  }
}